import { baseUrl, appid } from "../config.js";
import store from "@/store";

/**
 * progress : 
 */
export const uploadApi = (progress = null, chooseOptions = {}) => {
  const header = {};
  header.appid = appid;
  header.token = store.state.user.token;
  const url = baseUrl + "/mobile/upload";
  const fileName = "file";
  return new Promise((resolve, reject) => {
    const uploadTask = uni.chooseImage({
			...chooseOptions,
      success: (chooseImageRes) => {
        const tempFilePaths = chooseImageRes.tempFilePaths;
				uni.showLoading({
					title: '',
					mask: true
				})
        const uploadTask = uni.uploadFile({
          url,
          header,
          filePath: tempFilePaths[0],
          name: fileName,
          success: (res) => {
            const response = JSON.parse(res.data);
            if (res.statusCode === 200) {
              if (response.code === 20000 && response.msg !== "fail") {
                resolve(response.data);
                return;
              }
            }
            uni.showToast({
              title: response.data,
              icon: "none",
            });
            reject(response.data);
          },
          fail: (err) => {
            uni.showToast({
              title: err.message || ",",
              icon: "none",
            });
            reject(err.message);
          },
					complete() {
						uni.hideLoading()
					}
        });
        // 
        progress &&
          uploadTask.onProgressUpdate((res) => {
            progress({
              progress: res.progress,
              current: res.totalBytesSent,
              total: res.totalBytesExpectedToSend,
            });
          });
        //
      },
    });
  });
};
