// import store from '@/store'
export default () => {
	return new Promise((resolve, reject) => {
		uni.getSystemInfo({
			success: e => {
				// , 
				const areaTop = e
					.safeAreaInsets.top;
				const statusBar = e
					.statusBarHeight;
				const areaBottom = e
					.safeAreaInsets.bottom;
				let navigator =
					44 // h544px
				let customBar = 0
				//   ----- app ------














				//  -----  ------

				const wxMenuButton = wx
					.getMenuButtonBoundingClientRect();
				//  1.
				navigator = wxMenuButton
					.height + (wxMenuButton
						.top - statusBar) * 2;
				//  2. + 
				customBar = navigator +
					statusBar


				// -----  ------








				const info = {
					info: e, //  systemInfo  
					platform: e
						.platform, // 
					areaTop, //   
					areaBottom,
					statusBar, //  ( ,  )
					customBar, //  + 
					navigator //   ( : ios:45, android:50, h5:44 )
				}
				// appH5 ( : ios:45, android:50, h5:44 )
				resolve(info)
			}
		});
	})

}
